import type { Metadata } from "next";
import "./../styles/globals.css";
import { Inter, Space_Grotesk } from "next/font/google";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import ScrollProgressBar from "../components/ScrollProgressBar";
import CursorGlow from "../components/CursorGlow";

const inter = Inter({
  subsets: ["latin"],
  variable: "--font-inter",
  display: "swap"
});

const spaceGrotesk = Space_Grotesk({
  subsets: ["latin"],
  variable: "--font-space-grotesk",
  display: "swap"
});

export const metadata: Metadata = {
  metadataBase: new URL("https://mir-suhaib-portfolio.example"), // replace when deployed
  title: "Mir Suhaib — Engineering Intelligent Systems",
  description:
    "Portfolio of Mir Suhaib, final-year CSE student from the University of Kashmir, focused on Machine Learning, Data Science, and Full Stack Development.",
  authors: [{ name: "Mir Suhaib" }],
  openGraph: {
    title: "Mir Suhaib — Engineering Intelligent Systems",
    description:
      "AI-driven, data-focused portfolio showcasing projects in Machine Learning, Data Science, and Full Stack Development.",
    type: "website",
    url: "https://mir-suhaib-portfolio.example",
    locale: "en_IN"
  },
  twitter: {
    card: "summary_large_image",
    title: "Mir Suhaib — Engineering Intelligent Systems",
    description:
      "Final-year CSE student building intelligent, data-driven systems in ML, DS, and full stack."
  },
  keywords: [
    "Mir Suhaib",
    "Machine Learning",
    "Data Science",
    "Full Stack Development",
    "Portfolio",
    "University of Kashmir"
  ],
  themeColor: "#0a0a0a",
  alternates: {
    canonical: "/"
  }
};

export default function RootLayout({
  children
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en" className={`${inter.variable} ${spaceGrotesk.variable}`}>
      <body className="min-h-screen bg-background text-white flex flex-col">
        <div className="background-grid" />
        <div className="fixed inset-0 -z-10 hero-gradient" />
        <CursorGlow />
        <ScrollProgressBar />
        <Navbar />
        <main className="flex-1 pt-20 pb-10">{children}</main>
        <Footer />
      </body>
    </html>
  );
}

